import React, {Component}  from 'react';
import axios from 'axios';
import Surats from '../components/Surats';

class Blog extends Component {

	constructor(props) {
	    super(props)
	    this.state = { 
		  surats: []
	    }
  	}

  	componentDidMount() {
	    axios.get('https://al-quran-8d642.firebaseio.com/data.json?print=pretty')
	      .then(response => {
	      	console.log(response)
	        this.setState({ surats: response.data})
	    })
	    .catch(error => {
	      console.log('error', error)
	    })
  	}

  	render() {
    	return (
    		<div className="container">     
		        <h1>Al Quran</h1> 
		        <div className="row">             
		          <div className="col-md-12" >     
		            <h2>Surat Surat</h2>            
		            <div className="row">
			            <Surats
		                    surats = {this.state.surats}
		                />		                           
		            </div>            
		          </div>		          
		        </div>
		      </div>
		)
    }
}


export default Blog;