import React from 'react';

const surats = (props) => (
	props.surats.map((surat) => (		
		<div className="col-md-12" key={surat.nomor}>
		    <div className="card" style={{ margin: 10}}>              
		      <div className="card-body">              
		        <h5 className="card-title">Nama Surat: {surat.nama}</h5>
		        <h5 className="card-title">Arti: {surat.arti}</h5>
		        <h5 className="card-title">Jumlah Ayat: {surat.ayat}</h5>
		        <p className="card-title">Keterangan: {surat.keterangan}</p> 
		        <a className="btn btn-warning" role="button" href={surat.audio}>Audio</a>                        
		      </div>
		    </div>
		  </div>
	))
)

export default surats;