import React, {Component}  from 'react';
import axios from 'axios';

class Registrasi extends Component {

	constructor(props) {
	    super(props)
	    this.state = {  		
	      first_name : '',
	      email : '',
	      buttonDisabled : false,
	      formStatus : 'create',
	      nama_reg : '',
	      email_reg : '',
	      register: '',
	    }
  	}

  	inputOnChangeHandler = (event) => {
	    this.setState({ [event.target.name] : event.target.value})
  	}

  	onSubmitHandler = (e) => {
	    this.setState({ buttonDisabled: true})
	    console.log('form di submit')
	    e.preventDefault()
	    var payload = {	    	
	      first_name : this.state.first_name,
	      email : this.state.email,
	    }
	    var url = 'https://reqres.in/api/users'
	    
	    axios.post(url,payload)
	        .then(response => {
	          this.setState({
	          					buttonDisabled: false, 
	          					first_name: '', 
	          					email: '',
          						nama_reg : response.data.first_name,
						      	email_reg : response.data.email,
						      	register: 'Registered!'
					  		})
	        })
	        .catch(error => {
	          console.log(error)
      	})
  	}

	render() {
	    return (
	    	<div className="container">     
		        <h1 style={{textAlign: 'center'}}>Registrasi</h1> 
		        <br/>
		        <div className="row"> 
			        <div className="col-md-6" >  
			        	<img
			        		style = {{width: '50%', textAlign: 'center'}}
							src = "https://www.codepolitan.com/sites/codepolitan//themes/belajarcoding/assets/img/img-kuis.png"
							alt = "belajarCoding"
						/>
					</div>
		        </div>
		        <div className="row">      
		        	<div className="col-md-6" >     
			            <form onSubmit = {this.onSubmitHandler}>
					      <div className="form-group">
					        <label>Name</label>
					        <input type="text" className="form-control" name="first_name" value={this.state.first_name} onChange={this.inputOnChangeHandler}/>
					      </div>
					      <div className="form-group">
					        <label>Email</label>
					        <input type="email" className="form-control" name="email" value={this.state.email} onChange={this.inputOnChangeHandler}/>
					      </div>
					      <button type="submit" className="btn btn-success" disabled={this.buttonDisabled}>Submit</button>
					    </form>
		          	</div>       
		          	<div className="col-md-6" >        		            
			                <form>
						      <div className="form-group">
						        <label>Name</label>
						        <input type="text" className="form-control" name="nama_reg" value={this.state.nama_reg} onChange={this.onChange} disabled/>
						      </div>
						      <div className="form-group">
						        <label>Email</label>
						        <input type="email" className="form-control" name="email_reg" value={this.state.email_reg} onChange={this.onChange} disabled/>
						      </div>	
						      <h4>{this.state.register}</h4>           							
						    </form>        
		          	</div>		          
		        </div>		        
	      	</div>
		)
	}
}

export default Registrasi;