import React from 'react';
import Card from '../components/Card';
import Intro from '../components/Intro';

const intro = {
	content: `Tak ada cara instan untuk menjadi profesional, termasuk dalam belajar pemrograman dan menjadi seorang developer. Semua butuh proses, usaha dan kerja keras. Bangun karirmu dengan belajar pemrograman untuk menjadi seorang developer profesional. Mulai langkah pertamamu dengan bergabung dalam program Developer School!`	
}

const cards=[
	{ 
		title: 'Weekend Bootcamp', 
		imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg',
		subtile: 'Pilih kelas online yang ingin kamu ikuti sesuai dengan yang kamu butuhkan. Kelas online yang kamu ikuti bisa kamu pelajari SELAMANYA. Bonus DVD materi untuk belajar secara offline.'
	},
	{ 
		title: 'Intensive Bootcamp', 
		imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg',
		subtile: '6 bulan jadi Full Stack Developer, dengan belajar coding online. Belajar kapan saja dari mana saja. Akses semua materi belajar di CODEPOLITAN. Dapatkan sertifikat digital dengan menyelesaikan alur belajarnya.'
	}	
]

const DevSchool = () => (
	<div className="container">
		<h1 style={{textAlign: 'center'}}>Developer School</h1>
		<div className="row" style={{marginTop: 40, marginBottom:100}}>
			<Intro content={intro.content}/>
			<div className="col-md-6">
				<img
					src="https://devschool.id/resources/themes/default/assets/img/foto-devschool.jpg"
					alt=""
				/>
			</div>	
		</div>
		<div className="row" style={{ marginBottom:50}}>			
			{
				cards.map(card => (
					<Card
						imageUrl={card.imageUrl}
						title={card.title}
						subtitle={card.subtitle}
					/>
				))
			}
		</div>
	</div>
)

export default DevSchool;