import React from 'react';
import Card from '../components/Card';
import Intro from '../components/Intro';

const intro = {
	content: `Tak ada cara instan untuk menjadi profesional, termasuk dalam belajar pemrograman dan menjadi seorang developer. Semua butuh proses, usaha dan kerja keras. Bangun karirmu dengan belajar pemrograman untuk menjadi seorang developer profesional. Mulai langkah pertamamu dengan bergabung dalam program Developer School!`	
}

const features =[
	{ title: 'Online Course', desc: 'Belajar coding dengan format video yang telah disusun secara sistematis dilengkapi dengan contoh kasus pengembangan proyek aplikasi'},
	{ title: 'Interactive Coding', desc: 'Belajar online dasar-dasar pemrograman dengan konsep belajar interaktif dimana pengguna dapat menulis kode program dan menjalankannya secara online'},
	{ title: 'Live Webinar', desc: 'Seminar online melalui video conference membahas info, tutorial dan tips seputar pemrograman dan teknologi diselenggarakan rutin 2 kali seminggu'}
]

const cards=[
	{ title: 'Weekend Bootcamp', imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg'},
	{ title: 'Intensive Bootcamp', imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg'}	
]

const Premium = () => (
	<div className="container">
		<h1 style={{textAlign: 'center'}}>Pemium Online Course</h1>
		<div className="row" style={{marginTop: 0, marginBottom:100}}>
			<Intro content={intro.content}/>
			<div className="col-md-6" style={styles.box450border}>
				{
					features.map(feature => (
						<div>
							<h5><strong>{feature.title}</strong></h5>
							<p>{feature.desc}</p>
						</div>
					))
				}
			</div>	
		</div>
		<div className="row" style={{ marginBottom:50}}>			
			{
				cards.map(card => (
					<Card
						imageUrl={card.imageUrl}
						title={card.title}
					/>
				))
			}
		</div>
	</div>
)

const styles = {
	box450border: {
		height: '450px',
		border: 'px solid #f1f1f1',
		borderRadius: '10',
		padding: 30,
	}
}

export default Premium;
